@media only screen and (min-width: 1080px){

  *{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
  }


  body{
    height: 100vh;
    width: 100vw;
  }

  html{
    width: 100vw;
    height: 100vh;
  }
  
  a,li{
    text-decoration: none;
    list-style-type: none;
    color:aliceblue;
    font-size: 2.5rem;
    font-weight: 100;
  }
  
  a{
    color:aliceblue;
  }
  
  
  .laptop{
  position: fixed;
  
  
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  
  }

  
  ul{
    display: flex;
    width: 95vw;
    height: 17vh;
  justify-content: space-around;
 
 
  }
  
  body{
    height: 160vh;
  }

  

  .home{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-self: center;
    justify-content: center;
    background-color: black;
    text-align: center;
  }

  .title{
    font-size: 200;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    color: aliceblue;
 
   
  }

  
 



}


@media only screen and (max-width: 600px){
  *{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
  }

 

  .home{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-self: center;
    justify-content: center;
    text-align: center;
    background-color: black;
  }

  .title{
    font-size: 200;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    color: aliceblue;
  
   
  }

  
  a,li{
    text-decoration: none;
    list-style-type: none;
    color:aliceblue;
    font-size: 2.5rem;
    font-weight: 100;
  }

  .laptop{
    display: none;
  }


}